import { configureStore, ThunkAction, Action } from '@reduxjs/toolkit';
import agentChannelsSlice from '../../../features/agentChannels/AgentChannelsSlice';
import agentCommandsSlice from '../../../features/agentCommands/AgentCommandsSlice'
import InboundSlice from '../../../features/hub/InboundSlice';
import OutboundSlice from '../../../features/hub/OutboundSlice';
import loginSlice  from '../../../features/login/LoginSlice';
import  phoneBarSlice  from '../../../features/phoneBar/PhonebarSlice';
import notificationSlice from '../../../features/notification/ServerNotificationSlice';
import agentStateSlice from '../../../features/agentState/AgentStateSlice';
import { listenerMiddleware } from './listenerMiddleware';
import  preAuthUserLoginSlice  from '../../../features/preauthUserLogin/PreAuthUserLoginSlice';
import phoneInteractionSlice from '../../../features/phoneInteraction/PhoneInteractionSlice';
import ChatInteractionSlice from '../../../features/chatInteraction/ChatInteractionSlice';
import WorkItemListSlice from '../../../features/workItemList/WorkItemListSlice';
import WorkItemSlice from '../../../features/WorkItem/WorkItemSlice';

export const store = configureStore({
  reducer: {
    agentCommands: agentCommandsSlice,
    login: loginSlice,
    preAuthUserLogin: preAuthUserLoginSlice,
    notification: notificationSlice,
    phoneBar: phoneBarSlice,
    agentChannels: agentChannelsSlice,
    inbound: InboundSlice,
    outbound: OutboundSlice,
    agentState: agentStateSlice,
    phoneInteraction: phoneInteractionSlice,
    chatInteraction: ChatInteractionSlice,
    workItemList: WorkItemListSlice,
    workItem: WorkItemSlice

  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().prepend(listenerMiddleware.middleware),
});

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;
