import { useCallback, useEffect, useRef, useState } from "react"
import { WorkItemListChildren } from "../workItemList/WorkItemListSlice"
import styles from './WorkItem.module.scss'

import { check_18px as CheckIcon } from '../../assets/icons/check_18px';
import { close_24px as RejectIcon } from '../../assets/icons/close_24px';
import { chat_24px as ChatIcon } from '../../assets/icons/chat_24px';
import { call_24px as VoiceIcon } from '../../assets/icons/call_24px';

interface WorkItemProps {
    attributes: WorkItemListChildren["attributes"],
    components: WorkItemListChildren["components"],
    onWorkItemAcceptClick: any,
    onWorkItemDeclineClick: any,
    onWorkItemSelectedClick: any
}

const WorkItem: React.FC<WorkItemProps> = ({ attributes, components, onWorkItemAcceptClick, onWorkItemDeclineClick, onWorkItemSelectedClick }) => {


    const { life_time, country_code, update_channel_counter, caller, channel_type, current, opened, disconnected, transferred } = attributes;

    const [show_countdown, setShowCountdown] = useState(false);
    const [timer, setTimer] = useState(-1);
    const [ordered_components, setOrderedComponents] = useState(components);
    const [show_components, setShowComponents] = useState(false);

    useEffect(() => {
        console.log("ordered_components", ordered_components, show_countdown)
    }, [ordered_components, show_countdown])


    const updateTimer = useCallback((node: HTMLDivElement | null, timer_type: "accept_timer_countdown" | "decline_timer_countdown" | "accept" | "decline") => {
        if (node) {
            if (timer_type === "decline_timer_countdown" && timer > 0)
                node.style.setProperty('--timer', `${timer}s`)
            else {
                node.style.setProperty('--timer', `10s`)
                node.style.setProperty('--timer-infinite', `infinite`);
            }
        }
    }, [timer])

    useEffect(() => {
        // countdown timers
        for (let component of components) {
            if (component.name === "accept_timer_countdown" || component.name === "decline_timer_countdown") {
                setShowCountdown(component.visible);
                if (component.name === "decline_timer_countdown") {
                    if (component.value && component.value > 0 && timer < 0)
                        setTimer(component.value)
                }
            }
        }

        // sorting components
        const components_copy = [...components];
        components_copy.sort((a, b) => {
            if (a.name === "accept" || a.name === "decline")
                return a.name === "accept" ? -1 : 1
            else return 1

        })
        setOrderedComponents(components_copy)

        // show components

        setShowComponents(components.some(component => component.visible)) // if any component is visible, show the components wrapper
    }, [components])


    const handleComponentClick = (componentName: string) => {
        componentName === "accept" ?
            onWorkItemAcceptClick(attributes.work_item_id) :
            onWorkItemDeclineClick(attributes.work_item_id)
    }

    return (
        <>
            <div className={styles.wrapper}>
                <button
                    onClick={() => onWorkItemSelectedClick(attributes.work_item_id)}
                    className={`
                    ${styles.attributes_content} 
                    ${channel_type === "VOICE" ? styles.voice : styles.chat}
                    ${current ? styles.current : styles.not_active}
                    ${opened ? styles.opened : ""}
                    ${disconnected ? styles.disconnected : ""}
                    ${transferred ? styles.transferred : ""}
                    ${update_channel_counter && update_channel_counter > 0 ? styles.notification : ""}
                    `+ " noselect"}>
                    <div className={styles.col_attributes}>
                        {channel_type === "VOICE" ?
                            <VoiceIcon fill={`${!current ? "#37474F" : "#DADFEA"}`} /> :
                            <ChatIcon fill={`${!current ? "var(--bkg-main-dark)" : "var(--bkg-main-light)"}`} />}
                        <div >{caller}</div>
                    </div>

                    <div className={styles.col_attributes}>
                        <div id={"country_code"}>
                            {country_code}
                        </div>
                        {(update_channel_counter && update_channel_counter > 0) ? <div id="channel_counter" 
                        className={`${channel_type === "VOICE" ? styles.voice : styles.chat} ${styles.channel_counter }`}>{update_channel_counter}</div> : null}
                    </div>
                </button>

                {show_components && <div className={`${styles.components_wrapper} ${show_countdown && styles.curved_border}`}>
                    <div className={styles.content}>
                        {ordered_components?.map((component, index) => {
                            return (component.name === "accept" || component.name === "decline") ?
                                component.visible &&
                                <button
                                    id={component.name}
                                    onClick={() => handleComponentClick(component.name)}
                                    className={`${styles.component_btn} ${component.name === "accept" ? styles.accept_btn : null}`}
                                    disabled={!component.enabled} key={index}>
                                    {component.name === "accept" && <CheckIcon fill="var(--bkg-main-dark)" />}
                                    {component.name === "decline" && <RejectIcon fill="#fff" />}
                                </button> : show_countdown && component.visible && component.value && component.value > 0 &&
                                <div id={component.name} key={index} className={styles.pie} ref={node => updateTimer(node, component.name)}>
                                    <div className={styles.count_down_number}>
                                        <div className={styles.content}>
                                            {component.name === "decline_timer_countdown" ? component.value : null}
                                        </div>

                                    </div>
                                </div>

                        })}


                    </div>

                </div>}

            </div>


        </>


    )

}

export default WorkItem

