import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'
import { AbstractEvent } from '../../app/core/events/AbstractEvent'

interface LoginState {
  showLogin: boolean,
}

const initialState: LoginState = {
  showLogin: false,
}

export const loginSlice = createSlice({
  name: "login",
  initialState,
  reducers: {
    loginButtonClicked: (state, action: PayloadAction<AbstractEvent>) => {},
    loginShow: (state) => {state.showLogin = true},
    loginHide: (state) => {state.showLogin = false},
  },
   
})

export const { loginButtonClicked, loginHide, loginShow } = loginSlice.actions;
export default loginSlice.reducer

