import { createSlice } from "@reduxjs/toolkit";

export enum AgentStateValues {
    PAUSED = 'PAUSED',
    AVAILABLE = 'AVAILABLE',
    PAUSE_REQUESTED = 'PAUSE_REQUESTED',
    INIT_AVAILABLE = 'INIT_AVAILABLE',
    INITIALIZED = 'INITIALIZED',
    LOGGED_IN = 'LOGGED_IN',
    LOGGED_OUT = 'LOGGED_OUT',
    UNINITIALIZED = 'UNINITIALIZED',
}

interface AgentState {
    agentName: string;
    agentId: string;
    agentState: AgentStateValues;
    logoutFailureReason?: string;
    loginLifetime?: number;
    agentState_visible: boolean;
    startEnabled: boolean;
}

const initialState: AgentState = {
    agentName: '',
    agentId: '',
    agentState: AgentStateValues.UNINITIALIZED,
    logoutFailureReason: '',
    loginLifetime: 0,
    agentState_visible: false,
    startEnabled: false
}


const AgentStateSlice = createSlice({
    name: 'agentState',
    initialState,
    reducers: {
        toggleAgentStateStartEnabled: (state, action) => {
            state.startEnabled = action.payload;
        },
        toggleAgentStateVisibility: (state, action) => {
            state.agentState_visible = true;
            state.agentId = action.payload?.attributes?.agent_id;
            state.agentName = action.payload?.attributes?.agent_name;
            state.agentState = action.payload?.attributes?.agent_state;
            state.loginLifetime = action.payload?.attributes?.agent_login_life_time;
        },
        updateAgentState: (state, action) => {
            state.agentId = action.payload?.attributes?.agent_id;
            state.agentName = action.payload?.attributes?.agent_name;
            state.agentState = action.payload?.attributes?.agent_state;
            state.loginLifetime = action.payload?.attributes?.agent_login_life_time;
        },
        onStartClicked: (_, __) => {
        },
        onTerminateBreakClick: (_, __) => {
        }
    }
});

export const { toggleAgentStateStartEnabled, toggleAgentStateVisibility, updateAgentState, onStartClicked, onTerminateBreakClick } = AgentStateSlice.actions;
export default AgentStateSlice.reducer;
