import { PayloadAction, createSlice } from "@reduxjs/toolkit";

export interface AgentActionBarState {
    show: boolean;
}


const initialState: AgentActionBarState = {
    show: false,
}

const phoneInteractionSlice = createSlice({
    name: 'phoneInteraction',
    initialState,
    reducers: {
        onPhoneInteractionShow: (state, action: PayloadAction<AgentActionBarState>) => {
            state.show = true;
        }
    }
});

export const { onPhoneInteractionShow } = phoneInteractionSlice.actions;
export default phoneInteractionSlice.reducer;