import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export type InboundEventMessage = {
    code: string,
    params: object,
}

interface InboundState {
    inboundEventMessage: string
}

const initialState: InboundState = {
    inboundEventMessage: ''
}

const inboundSlice = createSlice({
    name: 'inbound',
    initialState,
    reducers: {
        notifyInboundMessage: (state, action: PayloadAction<any>) => {
            if(action.payload && action.payload.code)
            state.inboundEventMessage = JSON.stringify(action.payload);
        }
    }
})

export const { notifyInboundMessage } = inboundSlice.actions
export default inboundSlice.reducer
