import { ReactElement, forwardRef } from "react";
import styles from './SelectBreakPanel.module.scss'
import { FormattedMessage } from "react-intl";

interface BreakType {
    name: string;
    icon?: ReactElement<any, any> | undefined;
}

interface SelectBreakPanelProps {
    breakTypes: BreakType[],
    onClose: () => void,
    onBreakSelected: (channel: string) => void
}

const SelectBreakPanel = forwardRef<HTMLDivElement, SelectBreakPanelProps>(({breakTypes, onClose, onBreakSelected}, ref) => {
    const buttons = breakTypes.map((breakType) => {
        return <div className={styles.breakButton} key={breakType.name} onClick={() => onBreakSelected(breakType.name)}>
                    {breakType.icon ? breakType.icon : <></>}
                    {breakType.name}
               </div>
        });
    return <div className={styles.wrapper + ' shadowed'} ref={ref}>
        <header>
            <FormattedMessage
                id='agentCommands_break_button'
                description='Parametrized agent command'
                defaultMessage='break_button'
            />
            <button onClick={onClose}>X</button>
        </header>
        <section className={styles.break_types}>
            {buttons}
        </section>
        <section className={styles.break_controls}>
            <button onClick={onClose}>
                <FormattedMessage
                    id='agentCommands_break_cancel_button'
                    description='Cancel the selection of a break'
                    defaultMessage='cancel'
                />
            </button>
            <button onClick={onClose}>
                <FormattedMessage
                    id='agentCommands_break_save_button'
                    description='Save the selection of a break'
                    defaultMessage='save'
                />
            </button>
        </section>
    </div>
});

export default SelectBreakPanel;
