import { FormattedMessage } from "react-intl";
import { useAppSelector, useAppDispatch } from "../../app/core/hooks/hooks";
import WorkItem from "../WorkItem/WorkItem";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";
import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";
import { useEffect } from "react";
import styles from "./WorkItemList.module.scss";
import { WorkItemListPayload, WorkItemPayload, onWorkItemAcceptClick, onWorkItemDeclineClick, onWorkItemSelectedClick, showWorkItem, showWorkList, updateWorkItem, updateWorkList } from "./WorkItemListSlice";
import { agentCommandEnabledStateMap } from '../../app/core/utils';

const WorkItemList: React.FC<{}> = () => {

    const dispatch = useAppDispatch();

    const visible = useAppSelector((state) => state.workItemList.visible);
    const workItemList = useAppSelector((state) => state.workItemList.children);

    const onWorkItemAcceptClickHandler = (work_item_id: string) => {
        dispatch(onWorkItemAcceptClick({source: "workItemList", event: "onWorkItemAcceptClick", payload: {work_item_id} }))
    }

    const onWorkItemDeclineClickHandler = (work_item_id: string) => {
        dispatch(onWorkItemDeclineClick({source: "workItemList", event: "onWorkItemDeclineClick", payload: {work_item_id} }))
    }

    const onWorkItemSelectedClickHandler = (work_item_id: string) => {
        dispatch(onWorkItemSelectedClick({source: "workItemList", event: "onWorkItemSelectedClick", payload: {work_item_id} }))
    }

    enum WORKITEMLIST_ACTION_CODE {
        "work_list_show" = "work_list_show",
        "work_list_update" = "work_list_update",
        "work_item_show" = "work_item_show",
        "work_item_update" = "work_item_update",

    }

    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware();

    const workItemListHubEvents: { [key: string]: Function } = {
        [WORKITEMLIST_ACTION_CODE.work_list_show]: (params: WorkItemListPayload) => {
            dispatch(showWorkList(params))
        },
        [WORKITEMLIST_ACTION_CODE.work_list_update]: (params: WorkItemListPayload) => {
            dispatch(updateWorkList(params))
        },
        [WORKITEMLIST_ACTION_CODE.work_item_show]: (params: WorkItemPayload) => {
            dispatch(showWorkItem(params))
        },
        [WORKITEMLIST_ACTION_CODE.work_item_update]: (params: WorkItemPayload) => {
            dispatch(updateWorkItem(params))
        }
    }

    useEffect(() => {
        // register actions to be dispatched to hub
        setActions([onWorkItemAcceptClick, onWorkItemDeclineClick, onWorkItemSelectedClick])
        // subscribe to hub events
        setHubEvents(workItemListHubEvents)
    }, [])

    return (<> {agentCommandEnabledStateMap(visible) &&
        <>
            <div className={styles.wrapper}>

                <div className="workItemList__header">
                    <div className="workItemList__header__title">
                    </div>
                </div>
                <div className="workItemList__content">
                    <div className="workItemList__content__list">
                        {workItemList?.map((item, index) => {
                            return <WorkItem 
                            key={index} 
                            components={item.components} 
                            attributes={item.attributes} 
                            onWorkItemAcceptClick={onWorkItemAcceptClickHandler}
                            onWorkItemDeclineClick={onWorkItemDeclineClickHandler}
                            onWorkItemSelectedClick={onWorkItemSelectedClickHandler}
                             />
                        })}
                    </div>
                </div>



            </div>

        </>
    }
    </>)

}


export default WorkItemList;
