import { InboundEventMessage } from "../../features/hub/InboundSlice";

export const typeFromArray = (arr: string[]) => arr.map((item) => typeof `'${item}'`).join(' | ');

export const typeFromObject = (obj: { [key: string]: any }) => Object.keys(obj).map((item) => `'${item}'`).join(' | ');

export const typeFromObjectValues = (obj: { [key: string]: any }) => Object.values(obj).map((item) => `'${item}'`).join(' | ');

export const serialize = (obj: { [key: string]: any }) => JSON.stringify(obj, null, 2);

export const deserializeInboundEventMessage = (str: string): InboundEventMessage => JSON.parse(str);

export const getKeys = (obj: { [key: string]: any }) => Object.keys(obj);

export const getValues = (obj: { [key: string]: any }) => Object.values(obj);

export const timeFormat = (time: number | undefined) => {
    if (time === undefined) return '00:00:00';
    var date = new Date(0);
    date.setSeconds(time); // specify value for SECONDS here
    return date.toISOString().substring(11, 19);
}

export const agentCommandEnabledStateMap = (enabled: string | boolean) => {
    return enabled === 'true' || enabled === true;
}
