import { useEffect } from "react";
import { FormattedMessage } from "react-intl";
import { useAppDispatch, useAppSelector } from "../../app/core/hooks/hooks";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";
import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";
import Spinner from "../spinner/Spinner";
import { onStartClicked, onTerminateBreakClick, toggleAgentStateStartEnabled, toggleAgentStateVisibility, updateAgentState, AgentStateValues } from "./AgentStateSlice";
import styles from './AgentState.module.scss';
import Timer from "../../app/core/components/Timer";
import AgentName from "../../app/core/components/AgentName";
import { OnTerminateBreakClick, OnStartClicked } from "./AgentStateEvents";
import _ from 'lodash';
import { agentCommandEnabledStateMap } from '../../app/core/utils';

const AgentState = () => {

    const dispatch = useAppDispatch();

    const agentState = useAppSelector((state) => state.agentState);
    const agentCommandsState = useAppSelector((state) => state.agentCommands);

    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware();

    const AGENT_STATE_HUB_EVENT_CODE = {
        agent_state_show: 'agent_state_show',
        agent_state_update: 'agent_state_update',
    }

    const agentStateHubEvents: { [key: string]: Function } = {
        [AGENT_STATE_HUB_EVENT_CODE.agent_state_show]: (params: any) => {
            dispatch(toggleAgentStateVisibility(params))
        },
        [AGENT_STATE_HUB_EVENT_CODE.agent_state_update]: (params: any) => {
            dispatch(updateAgentState(params))
        }
    }

    useEffect(() => {
        // register actions to be dispatched
        setActions([onStartClicked, onTerminateBreakClick])
        // subscribe to hub events
        setHubEvents(agentStateHubEvents)
    }, [])

    useEffect(() => {
        if(agentState.agentState === AgentStateValues.PAUSED) {
            dispatch(onTerminateBreakClick(OnTerminateBreakClick));
        }
    }, [agentState.agentState]);

    useEffect(() => {
        const agentCommands = _.groupBy(agentCommandsState.components, 'name');
        const startComponent = agentCommands['start_button']?.pop() || {
            enabled: true,
            name: 'start_button',
            visible: true
        };
        dispatch(toggleAgentStateStartEnabled(agentCommandEnabledStateMap(startComponent.enabled)));
    }, [agentCommandsState.components]);

    const agentStateMapping = (agentState: AgentStateValues) => {
        switch (agentState) {
            case AgentStateValues.AVAILABLE:
                return 'ready';
            case AgentStateValues.PAUSED:
            case AgentStateValues.LOGGED_IN:
            case AgentStateValues.UNINITIALIZED:
            case AgentStateValues.INIT_AVAILABLE:
            case AgentStateValues.INITIALIZED:
            case AgentStateValues.LOGGED_OUT:
                return 'start';
            default:
                return 'start';
        }
    }

    const agentStateContent = (agentState: AgentStateValues) => {
        switch (agentState) {
            case AgentStateValues.AVAILABLE:
                return <FormattedMessage
                            id='agentState_ready'
                            description='Mapped agent state'
                            defaultMessage='ready'
                        />
            case AgentStateValues.PAUSED:
            case AgentStateValues.LOGGED_IN:
            case AgentStateValues.UNINITIALIZED:
            case AgentStateValues.INIT_AVAILABLE:
            case AgentStateValues.INITIALIZED:
            case AgentStateValues.LOGGED_OUT:
                return <FormattedMessage
                            id='agentState_start'
                            description='Mapped agent state'
                            defaultMessage='start'
                        />
            default:
                return <FormattedMessage
                            id='agentState_start'
                            description='Mapped agent state'
                            defaultMessage='start'
                        />
        }
    }

    const content = agentState.agentState_visible ?
        <div className={styles.wrapper}>
            <div className={styles.agent_info}>
                <div className=" width-100 flex flex__align-center flex__start truncate">
                    <AgentName agentName={agentState.agentName} />
                </div>
                <div className=" width-100 flex flex__align-center flex__end ">
                    <Timer timer={agentState.loginLifetime} />
                </div>
            </div>
            <button className={`${styles.agent_status} ${styles[agentStateMapping(agentState.agentState)]}`}
                disabled={!agentState.startEnabled}
                onClick={() => dispatch(onStartClicked(OnStartClicked))}>
                {agentStateContent(agentState.agentState)}
            </button>
        </div> : <Spinner />;

    return <>{content}</>;
};

export default AgentState;
