
// store slice about phone bar

import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { stat } from 'fs';

export enum PermissionStatus {
    INIT_REQUIRED, REQUESTING, GRANTED, REJECTED
}

interface NotificationState {
    status: PermissionStatus;
    timeout: number;
    close_on_click: boolean;
    require_active_browser: boolean;
}

const initialState: NotificationState = {
    status: PermissionStatus.INIT_REQUIRED,
    timeout: 10,
    close_on_click: true,
    require_active_browser: false
};

// define async actions

export const notificationSlice = createSlice({
    name: 'notificationSlice',
    initialState,
    reducers: {
        askPermission: (state, action) => {
            state.status = PermissionStatus.REQUESTING;
            state.close_on_click = action.payload.close_on_click;
            state.timeout = action.payload.timeout;
            state.require_active_browser = action.payload.close_on_click;
        },
        showNotification: (state, action) => {
            new Notification('', {
                body: action.payload.message,
                requireInteraction: state.close_on_click,
                silent: false,
                vibrate: [200, 100, 200],
            });
            // TODO: add timeout
            // TODO: require active browser
        },
        setPermission: (state, { payload }) => {
            if(payload === 'granted') {
                state.status = PermissionStatus.GRANTED;
            } else {
                state.status = PermissionStatus.REJECTED;
            }
        }
    },
});

export const { askPermission, setPermission, showNotification } = notificationSlice.actions;
export default notificationSlice.reducer;
