// store slice about agent channels


import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AbstractPayloadEvent } from '../../app/core/events/AbstractEvent';

// events sent to HUB

export enum CHANNEL_NAME {
    VOICE = "VOICE",
    EMAIL = "EMAIL",
    CHAT = "CHAT",
    SOCIAL = "SOCIAL",

}

export enum CHANNEL_STATE {
    ACTIVE = "ACTIVE",
    BUSY = "BUSY",
    IDLE = "IDLE",
    INITIALIZED = "INITIALIZED",
    INITIALIZING = "INITIALIZING",
    LOGGED_IN = "LOGGED_IN",
    LOGGED_OUT = "LOGGED_OUT",
    OCCUPIED = "OCCUPIED",
    UNINITIALIZED = "UNINITIALIZED",
    UNINITIALIZING = "UNINITIALIZING",
}


enum DELIVERY_STATE {
    UP = "UP",
    DOWN = "DOWN"
}


export interface AgentChannel {
    id: string
    name: CHANNEL_NAME
    state: CHANNEL_STATE
    enabled: string
    on: string
    delivery_state: DELIVERY_STATE
}

interface AgentChannelsState {
    visible: boolean
    agentChannels: AgentChannel[]

}

const initialState: AgentChannelsState = {
    visible: false,
    agentChannels: []
}


export const agentChannelsSlice = createSlice({
    name: 'agentChannels',
    initialState,
    reducers: {
        onAgentChannelsInitialized: (state, action: PayloadAction<{} | { components: AgentChannel[] }>) => {
            state.visible = true;
            if ('components' in action.payload) {
                state.agentChannels = action.payload.components;

            }
        },
        onAgentChannelsUpdate: (state, action: PayloadAction<{} | { components: AgentChannel[] }>) => {
            if ('components' in action.payload) {
                state.agentChannels = action.payload.components;
            }
        },
        channelButtonClicked: (state, action: PayloadAction<AbstractPayloadEvent>) => {
        }

    }
})

export const { onAgentChannelsInitialized, onAgentChannelsUpdate, channelButtonClicked } = agentChannelsSlice.actions
export default agentChannelsSlice.reducer




