import { createSlice, PayloadAction } from "@reduxjs/toolkit";


interface WorkItemListState {
    children: WorkItemListChildren[],
    visible: boolean
}

type COMPONENT_NAME = "accept_timer_countdown" | "decline_timer_countdown" | "accept" | "decline";

interface COMPONENT { visible: boolean, name: COMPONENT_NAME, enabled: boolean, value?: number }

interface ATTRIBUTES {
    life_time: number,
    country_code?: string,
    caller: string,
    called: string,
    work_item_id: string,
    channel_sub_type?: string,
    channel_type?: string,
    update_channel_counter?: number,
    current: boolean,
    opened: boolean,
    disconnected: boolean,
    transferred: boolean,
}

export interface WorkItemListChildren {
    "components": COMPONENT[],
    "attributes": ATTRIBUTES
}

export interface WorkItemListPayload {
    "children": WorkItemListChildren[],
    "attributes": {
        "visible": boolean
    }
}

export interface WorkItemPayload {
    "components": COMPONENT[],
    "attributes": ATTRIBUTES
}

const initialState: WorkItemListState = {
    children: [],
    visible: false
};

interface WorkItemActionPayload {
    source: string
    event: string,
    payload: {
        work_item_id: string
    }
}


export const workItemListSlice = createSlice({
    name: 'workItemList',
    initialState,
    reducers: {
        showWorkList: (state, action: PayloadAction<WorkItemListPayload>) => {
            state.visible = action.payload.attributes.visible;
            state.children = action.payload.children;
        },
        updateWorkList: (state, action: PayloadAction<WorkItemListPayload>) => {
            state.visible = action.payload.attributes.visible;
            state.children = action.payload.children;
        },
        updateWorkItem: (state, action: PayloadAction<WorkItemPayload>) => {
            const workItem = state.children.find((item) => item.attributes.work_item_id === action.payload.attributes.work_item_id);
            if (workItem) {
                workItem.components = action.payload.components;
                workItem.attributes = action.payload.attributes;
            }
        },
        showWorkItem: (state, action: PayloadAction<WorkItemPayload>) => {
            const workItem = state.children.find((item) => item.attributes.work_item_id === action.payload.attributes.work_item_id);
            if (workItem) {
                workItem.components = action.payload.components;
                workItem.attributes = action.payload.attributes;
            }
        },
        onWorkItemAcceptClick: (state, action: PayloadAction<WorkItemActionPayload>) => {
        },
        onWorkItemDeclineClick: (state, action: PayloadAction<WorkItemActionPayload>) => {
        },
        onWorkItemSelectedClick: (state, action: PayloadAction<WorkItemActionPayload>) => {
        }
    }
});

export const { showWorkList, updateWorkList, updateWorkItem, showWorkItem, onWorkItemAcceptClick, onWorkItemDeclineClick, onWorkItemSelectedClick } = workItemListSlice.actions;

export default workItemListSlice.reducer;
