import { useEffect, useState } from "react";
import { notifyInboundMessage } from "../../../features/hub/InboundSlice";
import { startAppListening } from "../store/listenerMiddleware";

export const useInboundMiddleware = () => {

    // component registered actions to be dispatched when a message is received from the hub

    const [hubEvents, setHubEvents] = useState<{ [key: string]: Function } | null>(null)

    useEffect(() => {
        if (!hubEvents) return
        const unsubscribe = startAppListening({
            actionCreator: notifyInboundMessage,
            effect: async (action, listenerApi) => {
                // side effect to update the store
                const { view, command, payload } = action.payload;
                const code = `${view}_${command}`;
                if (code in hubEvents) {
                    console.log('useInbound: notifyInboundMessage: ', code)
                    console.log('useInbound: notifyInboundMessage: ', payload)
                    // code = object_command
                    hubEvents[code](payload)
                }
            }
        })

        return unsubscribe
    }, [hubEvents])

    return [setHubEvents]
}
