import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { AbstractEvent, AbstractPayloadEvent } from '../../app/core/events/AbstractEvent';


interface OutboundState {
    eventMessage: string
}

const initialState: OutboundState = {
    eventMessage: ''
}

export const outboundSlice = createSlice({
    name: 'outbound',
    initialState,
    reducers: {
        notifyOutboundMessage: (state, action: PayloadAction<AbstractEvent | AbstractPayloadEvent>) => {
            const serializedEventMessage = JSON.stringify(action.payload);
            state.eventMessage = serializedEventMessage
        }
    },
    extraReducers: (builder) => {
    }
})

export const { notifyOutboundMessage } = outboundSlice.actions
export default outboundSlice.reducer


