import { Modal } from "react-bootstrap";
import { FormattedMessage } from "react-intl";

interface AskPermitionModalProps {
    onAccept: () => void;
    onReject: () => void;
    show: boolean;
}
const AskPermitionModal: React.FC<AskPermitionModalProps> = ({ onAccept, onReject, show }) => {
    return (
        <Modal show={show}>
          <Modal.Header>
            <Modal.Title>
                <FormattedMessage
                    id='desktop_notification_title'
                    description='Desktop notification title'
                    defaultMessage='Request for desktop notifications'
                />
            </Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <p>
                <FormattedMessage
                    id='desktop_notification_body'
                    description='Desktop notification body'
                    defaultMessage='Do you accept to receive desktop notifications from Iride Mediabar?'
                />
            </p>
          </Modal.Body>

          <Modal.Footer>
            <button className="my_btn" onClick={onReject}>
                <FormattedMessage
                    id='desktop_notification_skip'
                    description='Desktop notification skip button'
                    defaultMessage='Skip'
                />
            </button>
            <button className="my_btn" onClick={onAccept}>
                <FormattedMessage
                    id='desktop_notification_request'
                    description='Desktop notification request button'
                    defaultMessage='Request'
                />
            </button>
          </Modal.Footer>
        </Modal>
    );
  }

  export default AskPermitionModal;
