
import { useEffect, useMemo, useRef, useState } from "react";
import { FormattedMessage } from "react-intl";
import { useAppDispatch, useAppSelector } from "../../app/core/hooks/hooks";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";
import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";
import Logo from "../logo/Logo";
import styles from './Login.module.scss';
import { loginButtonClicked, loginHide, loginShow } from "./LoginSlice";
import { OnLoginClickEvent } from "./LoginEvents";
import { showPhonebar } from "../phoneBar/PhonebarSlice";
import Draggable from "../../app/core/components/Draggable";

const LoginView: React.FC<{}> = ({ }) => {

    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware();

    const elmtRef = useRef<HTMLDivElement>(null);

    const show = useAppSelector((state) => state.login.showLogin);

    const [showLoginAlert, setShowLoginAlert] = useState<boolean>(false);

    const dispatch = useAppDispatch();

    enum LOGIN_HUB_EVENT_CODE {
        login_show = 'login_show',
        login_hide = 'login_hide'
    }

    const loginHubEvents: { [key: string]: Function } = {
        [LOGIN_HUB_EVENT_CODE.login_show]: (params: any) => {
             dispatch(loginShow())
        },
        [LOGIN_HUB_EVENT_CODE.login_hide]: (params: any) => {
            dispatch(loginHide())
            //TODO
            // remove showPhonebar dispatch
            dispatch(showPhonebar())
        }
    }

    useEffect(() => {
        // register actions to be dispatched
        setActions([loginButtonClicked])
        // subscribe to hub events
        setHubEvents(loginHubEvents)
    }, [])


    const onLoginButtonClicked = () => {
        dispatch(loginButtonClicked(OnLoginClickEvent));
    }

    const onShowAlertBtnClick = () => {
        console.log("show alert")
    }

    const LoginAlert = useMemo(() => {
        return <div className="alert alert-danger" role="alert">
            <FormattedMessage
                id='login_force_confirmation'
                description='Prompt for forced login'
                defaultMessage='A session for agent already exists. Would you confirm the disconnection the current session?'
            />
            <div>

                <button className="my_btn" onClick={() => onShowAlertBtnClick()}>
                    <FormattedMessage
                        id='login_force_button_cancel'
                        description='Cancel the forced login'
                        defaultMessage='Cancel'
                    />
                </button>
            </div>
        </div>
    }, [])

    return (
        <>
            {show && 
              <Draggable elmnt={elmtRef}>
                <div ref={elmtRef} className={styles.wrapper}>
                    <header className={styles.header}>
                        <FormattedMessage
                            id='login_title'
                            description='Login title'
                            defaultMessage='Login'
                        />
                    </header>

                    <div  className={styles.content}>
                        <Logo />
                        <button className={styles.login_btn + " my_btn"} onClick={() => onLoginButtonClicked()}>
                        <FormattedMessage
                            id='login_button'
                            description='Login button'
                            defaultMessage='Login'
                        />
                    </button>
                    </div>

                </div>
                </Draggable>
            }
        </>
    )
}

export default LoginView