import { ActionCreatorWithPayload, addListener, isAnyOf } from "@reduxjs/toolkit";
import { useEffect, useState } from "react";
import { notifyOutboundMessage } from "../../../features/hub/OutboundSlice";
import { useAppDispatch } from "./hooks";

const useRegisteredEventMiddleware = () => {

    const dispatch = useAppDispatch();
    const [actions, setActions] = useState<ActionCreatorWithPayload<any>[]>([])

    useEffect(() => {
        if (actions.length === 0) return
        // attach listener to notifyOutboundMessage
        const unsubscribe = dispatch(addListener(
            {
                matcher: isAnyOf(...actions),
                effect: (action, listenerApi) => {
                    dispatch(notifyOutboundMessage(action.payload))
                },
            }))
        // detach listener on unmount
        return unsubscribe
    }, [actions])

    return [setActions]

}

export default useRegisteredEventMiddleware;
