import { PropsWithChildren, useEffect, useRef, useState } from "react";


const Draggable: React.FC<PropsWithChildren<{ elmnt: React.RefObject<HTMLDivElement> }>> = ({ children, elmnt }) => {


    const [dragging, setDragging] = useState(false);
    const [newStyle, setNewStyle] = useState({ top: "", left: "" })

    var pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;

    useEffect(() => {

        if (elmnt.current?.getElementsByTagName("header")) {

            const header = elmnt.current?.getElementsByTagName("header")[0]
            header.onmousedown = dragMouseDown;
            header.style.setProperty("cursor", dragging ? "grabbing" : "grab");
            elmnt.current.style.setProperty("position", "absolute")
            elmnt.current.style.setProperty("top", newStyle.top)
            elmnt.current.style.setProperty("left", newStyle.left)
        }

    }, [newStyle, dragging])


    function dragMouseDown(e: MouseEvent) {
        e = e || window.event;
        e.preventDefault();
        // get the mouse cursor position at startup:
        pos3 = e.clientX;
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag;
        setDragging(true)
    }

    function elementDrag(e: MouseEvent) {
        e = e || window.event;
        e.preventDefault();
        // calculate the new cursor position:
        pos1 = pos3 - e.clientX;
        pos2 = pos4 - e.clientY;
        pos3 = e.clientX;
        pos4 = e.clientY;
        // set the element's new position:
        const reactDiv = elmnt.current?.getBoundingClientRect();
        if (reactDiv) {
            const style = { top: (reactDiv.top - pos2) + "px", left: (reactDiv.left - pos1) + "px" };
            setNewStyle(style)

        }
    }

    function closeDragElement() {
        // stop moving when mouse button is released:
        document.onmouseup = null;
        document.onmousemove = null;
        setDragging(false)

    }


    return (
        <>
            {children}
        </>
    );
};


export default Draggable;