
import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../app/core/hooks/hooks";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";
import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";
import { onLogoutClick, updateAgentCommands } from "./AgentCommandsSlice";
import Spinner from "../spinner/Spinner";
import styles from './AgentCommands.module.scss';
import Separator from "../separator/Separator";
import { BookCommand, LogoutCommand, OutboundCommand, StartCommand } from "./AgentCommandButtons";
import _ from 'lodash';
import { LogoutClickEvent } from "./AgentCommandsEvents";

// Mediabar Commands

const AgentCommands: React.FC<{}> = () => {

    const dispatch = useAppDispatch();

    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware();
    const agentCommandsState = useAppSelector((state) => state.agentCommands);

    const AGENT_COMMANDS_HUB_EVENT_CODE = {
        agent_commands_show: 'agent_commands_show',
        agent_commands_update: 'agent_commands_update',
    }

    const agentCommandHubEvents: { [key: string]: Function } = {
        [AGENT_COMMANDS_HUB_EVENT_CODE.agent_commands_update]: (params: any) => {
            dispatch(updateAgentCommands(params));
        },
        [AGENT_COMMANDS_HUB_EVENT_CODE.agent_commands_show]: (params: any) => {
            dispatch(updateAgentCommands(params));
        }
    }

    useEffect(() => {
        // register actions to be dispatched to hub
        setActions([onLogoutClick])
        // subscribe to hub events
        setHubEvents(agentCommandHubEvents)
    }, [])

    const fireOnBreakButtonClicked = () => {
        console.log('BookCommand button clicked')
    }

    const fireOnStartButtonClicked = () => {
        console.log('StartCommand button clicked')
    }

    const setOutboundButtonClicked = () => {
        console.log('OutboundCommand button clicked')
    }

    const setLogoutButtonClicked = () => {
        console.log('LogoutCommand button clicked')
        dispatch(onLogoutClick(LogoutClickEvent))
    }

    const elements = _.groupBy(agentCommandsState.components, 'name');
    return (
        agentCommandsState.initialized ?
            <section className={styles.wrapper}>
                <div className={styles.content}>
                    <div className={styles.command_wrapper}>
                        {/* <StartCommand component={elements['start_button']?.pop()} onClick={fireOnStartButtonClicked} /> */}
                        <OutboundCommand component={elements['outbound_button']?.pop()} onClickHandler={setOutboundButtonClicked} />
                        <BookCommand component={elements['break_button']?.pop()} onClickHandler={fireOnBreakButtonClicked} />
                    </div>

                    <footer className={styles.footer_wrapper}>
                        <Separator />
                        <div className={styles.content}>
                            <LogoutCommand component={elements['logout_button']?.pop()} onClickHandler={setLogoutButtonClicked} />

                        </div>
                    </footer>
                </div>

            </section> : <Spinner />
    )

}

export default AgentCommands;


