import { useEffect, useState } from "react";
import { Subscription, catchError, concatMap, finalize, map, of, tap, timer } from "rxjs";
import { notifyInboundMessage } from "../../../features/hub/InboundSlice";
import { ws_connect, ws_connected, ws_disconnected, ws_error } from "../../../features/phoneBar/PhonebarSlice";
import { socket } from "../webSocket";
import { useAppDispatch, useAppSelector } from "./hooks";

// event codes from hub

export const useInOutImplementation = () => {

    const dispatch = useAppDispatch()

    const [subscription, setSubscription] = useState<Subscription>();
    const connecting = useAppSelector(state => state.phoneBar.wsConnecting);

    const [ws_$] = useState(
        socket.getWebSocket().pipe(
            concatMap((value, index) => index === 0
                ? of(value).pipe(
                    tap(() => dispatch(ws_connected())),
                )
                : of(value)
            ),
            catchError((err) => {
                dispatch(ws_error());
                dispatch(ws_disconnected());
                return of("web socket error")
            }),

            concatMap((data) => timer(100).pipe(map(() => { return data }))),
            finalize(() => dispatch(ws_disconnected())

            )
        )
    )

    const emit = (data: object) => {
        socket.emit(data);
    }

    useEffect(() => {
        dispatch(ws_connect());
    }, [])

    useEffect(() => {

        let sub: Subscription;
        if (connecting) {
            sub = ws_$.subscribe(
                {
                    next: inboundMsg => dispatch(notifyInboundMessage(inboundMsg)),
                    error: e => console.log(e),
                    complete: () => console.log("complete")
                }
            );
            setSubscription(sub);
        }

        return () => {

            if (sub && !connecting)
                return sub?.unsubscribe();
        }

    }, [connecting]);

    const ws_disconnection = () => {
        subscription?.unsubscribe();
        setSubscription(undefined);
    }

    const ws_connection = () => {
        if (connecting) return;
        dispatch(ws_connect())
    }


    return { emit, ws_disconnection, ws_connection }

}
