// store slice for pre-authenticated user login

import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';

type PreAuthUserLoginActions = "loginButtonClicked";

export interface PreAuthUserLoginState {
    ssoEnabled: boolean;
    email: string;
    password: string;
    isLoginPending: boolean;
    isLoginSuccess: boolean;
    isLoginError: boolean;
    loginButtonClicked: boolean
}

const initialState: PreAuthUserLoginState = {
    ssoEnabled: false,
    loginButtonClicked: false,
    email: '',
    password: '',
    isLoginPending: false,
    isLoginSuccess: false,
    isLoginError: false,
};


export const preAuthUserLoginSlice = createSlice({
    name: 'preAuthUserLogin',
    initialState,
    reducers: {
        setLoginButtonClicked: (state, action: PayloadAction<boolean>) => {
            state.loginButtonClicked = action.payload;
        },
        setEmail: (state, action: PayloadAction<string>) => {
            state.email = action.payload;
        },
        setPassword: (state, action: PayloadAction<string>) => {
            state.password = action.payload;
        },
        setLoginPending: (state, action: PayloadAction<boolean>) => {
            state.isLoginPending = action.payload;
        },
        setLoginSuccess: (state, action: PayloadAction<boolean>) => {
            state.isLoginSuccess = action.payload;
        },
        setLoginError: (state, action: PayloadAction<boolean>) => {
            state.isLoginError = action.payload;
        }
    },
});

export const { setLoginButtonClicked,setEmail, setPassword, setLoginPending, setLoginSuccess, setLoginError } = preAuthUserLoginSlice.actions;

export default preAuthUserLoginSlice.reducer;
