import { FormattedMessage } from "react-intl";
import { Component } from "./AgentCommandsSlice";
import styles from './AgentCommands.module.scss';
import { ReactElement, useRef } from "react";
import { break_24px as BreakIcon } from '../../assets/icons/break_24px';
import { log_out_24px as LogoutIcon } from '../../assets/icons/log_out_24px';
import { log_in_24px as LoginIcon } from '../../assets/icons/log_in_24px';
import { multicahnnel_outbound_24px as MultichannelIcon } from '../../assets/icons/multicahnnel-outbound_24px';
import OutboundPanel from "./OutboundPanel";
import { chat_24px as ChatIcon } from '../../assets/icons/chat_24px';
import { mail_24px as EmailIcon } from '../../assets/icons/mail_24px';
import { call_24px as VoiceIcon } from '../../assets/icons/call_24px';
import { social_24px as SocialIcon } from '../../assets/icons/social_24px';
import SelectBreakPanel from "./SelectBreakPanel";
import { agentCommandEnabledStateMap } from '../../app/core/utils';

interface CommandProps {
    component: Component | undefined,
    onClickHandler: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void | undefined
}

interface IconizedCommandProps extends CommandProps {
    icon: ReactElement<any, any> | undefined
}

const renderCommand: React.FC<IconizedCommandProps> = ({ component, onClickHandler, icon }, ref = undefined) => {
    return component && component.visible ?

        <button
            ref={ref}
            onClick={onClickHandler}
            disabled={!agentCommandEnabledStateMap(component.enabled)}
            className={" text_btn"}
        >
            <div>
                {icon ? icon : <></>}
                {" "}
                <FormattedMessage
                    id={'agentCommands_' + component.name}
                    description='Parametrized agent command'
                    defaultMessage={component.name}
                />
            </div>


        </button> : <></>
}

export const BookCommand: React.FC<CommandProps> = (props) => {

    const breakButtonRef = useRef<HTMLButtonElement>(null);
    const breakPanelRef = useRef<HTMLDivElement>(null);

    const setBookBreakButtonClicked = () => {
        if (breakPanelRef && breakPanelRef.current) {
            breakPanelRef.current.classList.toggle(styles.breakPanelVisible);
        }
        if (breakButtonRef && breakButtonRef.current) {
            breakButtonRef.current.classList.toggle(styles.agentButtonActive);
        }
    }

    const cmd = renderCommand({ ...props, onClickHandler: setBookBreakButtonClicked, icon: <BreakIcon fill='#FCFCFF' /> }, breakButtonRef);
    return <>
        <SelectBreakPanel breakTypes={[
            { name: 'coffee' },
            { name: 'lunch' },
            { name: 'on demand' },
        ]} ref={breakPanelRef}
            onClose={setBookBreakButtonClicked}
            onBreakSelected={(breakType) => console.warn('Not yet implemented, using break type', breakType)} />
        {cmd}
    </>;
}

export const LogoutCommand: React.FC<CommandProps> = (props) => {
    return renderCommand({ ...props, icon: <LogoutIcon fill='#FCFCFF' /> });
}

export const OutboundCommand: React.FC<CommandProps> = (props) => {

    const outboundButtonRef = useRef<HTMLButtonElement>(null);
    const outboundPanelRef = useRef<HTMLDivElement>(null);

    const setOutboundButtonClicked = () => {
        if (outboundPanelRef && outboundPanelRef.current) {
            outboundPanelRef.current.classList.toggle(styles.outboundPanelVisible);
        }
        if (outboundButtonRef && outboundButtonRef.current) {
            outboundButtonRef.current.classList.toggle(styles.agentButtonActive);
        }
    }

    const cmd = renderCommand({ component: props.component, onClickHandler: setOutboundButtonClicked, icon: <MultichannelIcon fill='#FCFCFF' /> }, outboundButtonRef);
    return <>
        <OutboundPanel channels={[
            { name: 'call', icon: <VoiceIcon fill="#888F9D" /> },
            { name: 'chat', icon: <ChatIcon fill="#888F9D" /> },
            { name: 'social', icon: <SocialIcon fill="#888F9D" /> },
            { name: 'email', icon: <EmailIcon fill="#888F9D" /> },
        ]} ref={outboundPanelRef}
            onClose={setOutboundButtonClicked}
            onOutboundSelected={(channel) => console.warn('Not yet implemented, using channel', channel)} />
        {cmd}
    </>
}

export const StartCommand: React.FC<CommandProps> = (props) => {
    return renderCommand({ ...props, icon: <LoginIcon fill='#FCFCFF' /> });
}
