
// store slice about phone bar

import { createSlice } from '@reduxjs/toolkit';

interface PhonebarState {
    showPhonebar: boolean;
    isPhonebarOpen: boolean;
    wsConnected: boolean;
    wsConnecting: boolean;
    wsDisconnected: boolean;
    wsError: boolean;
}

const initialState: PhonebarState = {
    showPhonebar: true,
    isPhonebarOpen: false,
    wsConnected: false,
    wsConnecting: false,
    wsDisconnected: false,
    wsError: false
};

// define async actions

export const phonebarSlice = createSlice({
    name: 'phoneBarSlice',
    initialState,
    reducers: {
        togglePhonebarOpen: (state) => {
            state.isPhonebarOpen = !state.isPhonebarOpen;
        },
        ws_connect: (state) => {
            state.wsConnecting = true;
        },
        ws_connected: (state) => {
            state.wsConnected = true;
            state.wsConnecting = false;
        },
        ws_disconnected: (state) => {
            state.wsConnected = false;
            state.wsConnecting = false;
            state.wsDisconnected = true;
        },
        ws_error: (state) => {
            state.wsError = true;
        },
        hidePhonebar: (state) => {
            state.showPhonebar = false;
        },
        showPhonebar: (state) => {
            state.showPhonebar = true;
        }
    }
});

export const { togglePhonebarOpen, ws_connect, ws_connected, ws_disconnected, ws_error, showPhonebar, hidePhonebar } = phonebarSlice.actions;

export default phonebarSlice.reducer;
