import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../app/core/hooks/hooks";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";
import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";
import AskPermitionModal from "./AskPermitionModal";
import { PermissionStatus, askPermission, setPermission, showNotification } from "./ServerNotificationSlice";

const ServerNotification: React.FC<{}> = () => {

    enum NOTIFICATION_ACTION_CODE {
        ask_permission = 'notification_ask_permission',
        message = 'notification_notification',
    }

    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware();

    const { status } = useAppSelector((state) => state.notification);

    const dispatch = useAppDispatch();

    const notificationHubEvents: { [key: string]: Function } = {
        [NOTIFICATION_ACTION_CODE.ask_permission]: (params: any) => {
          dispatch(askPermission(params))
        },
        [NOTIFICATION_ACTION_CODE.message]: (params: any) => {
          dispatch(showNotification(params))
        },
    }

    useEffect(() => {
        // register actions to be dispatched to hub
        setActions([])
        // subscribe to hub events
        setHubEvents(notificationHubEvents)
    }, [])

    const requestBrowserPermission = () => {
        Notification.requestPermission(() => {

        }).then((permission) => {
            dispatch(setPermission(permission));
        }).catch((error) => {
            console.error("Cannot grant notification permission", error);
            dispatch(setPermission('rejected'));
        });
    }

    const rejectBrowserPermission = () => {
        dispatch(setPermission('rejected'));
    }

    const hideInput = Notification.permission !== 'default';
    const show = !hideInput && status === PermissionStatus.REQUESTING;
    return hideInput ? <></> : <AskPermitionModal onAccept={requestBrowserPermission} onReject={rejectBrowserPermission} show={show} />;
}

export default ServerNotification;
