import { webSocket as wsrxjs } from 'rxjs/webSocket';


const gFQDN = window.location.hostname;
class webSocket {

    private useProtocol = window.location.protocol === 'https:' ? 'wss' : 'ws';
    private useHost     = window.location.hostname;
    private usePort     = window.location.port ? ':' + window.location.port : '';

    private WS_URL = process.env.NODE_ENV === "production" ?
    `${this.useProtocol}://${this.useHost}${this.usePort}/hub/mediabar/v2` : "ws://localhost:8999";

    private ws = wsrxjs(this.WS_URL)

    getWebSocket() {
        return this.ws.asObservable()
    }

    emit(data: object) {
        //this.ws.send(data);
        this.ws.next(data)
    }

}

export const socket = new webSocket();
