import { ReactElement, forwardRef } from "react";
import styles from './OutboundPanel.module.scss'
import { FormattedMessage } from "react-intl";

interface OutboundChannel {
    name: string;
    icon: ReactElement<any, any> | undefined;
}

interface OutboundPanelProps {
    channels: OutboundChannel[],
    onClose: () => void,
    onOutboundSelected: (channel: string) => void
}

const OutboundPanel = forwardRef<HTMLDivElement, OutboundPanelProps>(({channels, onClose, onOutboundSelected}, ref) => {
    const buttons = channels.map((channel) => {
        return <div className={styles.outboundButton} key={channel.name} onClick={() => onOutboundSelected(channel.name)}>
                    {channel.icon ? channel.icon : <></>}
                    {channel.name}
               </div>
        });
    return <div className={styles.wrapper + ' shadowed'} ref={ref}>
        <header>
            <FormattedMessage
                id='agentCommands_outbound_button'
                description='Parametrized agent command'
                defaultMessage='outbound_button'
            />
            <button onClick={onClose}>X</button>
        </header>
        <section>
            {buttons}
        </section>
    </div>
});

export default OutboundPanel;
