

import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface ChatInteraction {
    show: boolean;
    chatId: string;
    userId: string;
    lastMessage: string;
    lastMessageTime: string;
}

const initialState: ChatInteraction = {
    show: true,
    chatId: '',
    userId: '',
    lastMessage: '',
    lastMessageTime: ''

}

const chatInteractionSlice = createSlice({
    name: 'chatInteraction',
    initialState,
    reducers: {
        onChatInteractionShow: (state, action: PayloadAction<ChatInteraction>) => {
            state.show = false;
        }
    }
});

export const { onChatInteractionShow } = chatInteractionSlice.actions;
export default chatInteractionSlice.reducer;