import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { AbstractEvent } from "../../app/core/events/AbstractEvent";

// define state type
export type AgentBreakReason = string[]

export interface Component {
    visible: boolean,
    name: string,
    enabled: boolean
}

interface AgentCommandsState {
    initialized: boolean,
    components: Component[]
}


const initialState: AgentCommandsState = {
    initialized: false,
    components: [],
}


export const agentCommandsSlice = createSlice({
    name: 'agentCommands',
    initialState,
    reducers: {
        updateAgentCommands: (state, action) => {
            state.initialized = true
            state.components = action.payload.components
        },
        onLogoutClick: (state,action: PayloadAction<AbstractEvent>) => {}
    }
})

export const { updateAgentCommands, onLogoutClick } = agentCommandsSlice.actions
export default agentCommandsSlice.reducer;

