import { useEffect } from 'react';
import { FormattedMessage } from 'react-intl';
import { useAppDispatch, useAppSelector } from '../../app/core/hooks/hooks';
import { useInboundMiddleware } from '../../app/core/hooks/useInboundMiddleware';
import useRegisteredEventMiddleware from '../../app/core/hooks/useRegisteredEventMiddleware';
import { agentCommandEnabledStateMap } from '../../app/core/utils';
import { call_24px as VoiceIcon } from '../../assets/icons/call_24px';
import { chat_24px as ChatIcon } from '../../assets/icons/chat_24px';
import { mail_24px as EmailIcon } from '../../assets/icons/mail_24px';
import { social_24px as SocialIcon } from '../../assets/icons/social_24px';
import { OnChatClicked, OnEmailClicked, OnSocialClicked, OnVoiceClicked } from './AgentChannelEvents';
import styles from './AgentChannels.module.scss';
import { AgentChannel, CHANNEL_NAME, CHANNEL_STATE, channelButtonClicked, onAgentChannelsInitialized, onAgentChannelsUpdate } from './AgentChannelsSlice';

const AgentChannels: React.FC<{}> = () => {

    const dispatch = useAppDispatch();
    const channels = useAppSelector((state) => state.agentChannels.agentChannels);
    const [setActions] = useRegisteredEventMiddleware();
    const [setHubEvents] = useInboundMiddleware()
    const agentState = useAppSelector((state) => state.agentState.agentState);

    enum HUB_AGENT_CHANNEL_EVENT {
        agent_channels_show = 'agent_channels_show',
        agent_channels_update = 'agent_channels_update'
    }

    const agentChannelHubEvent = {
        [HUB_AGENT_CHANNEL_EVENT.agent_channels_show]: (params: any) => {
            dispatch(onAgentChannelsInitialized(params))
        },
        [HUB_AGENT_CHANNEL_EVENT.agent_channels_update]: (params: any) => {
            dispatch(onAgentChannelsUpdate(params))
        }
    }

    useEffect(() => {
        setActions([channelButtonClicked])
        setHubEvents(agentChannelHubEvent)
    }, []);

    const handleChannelClick = (channel: AgentChannel) => {
        let event;

        switch (channel.name) {
            case CHANNEL_NAME.CHAT:
                event = OnChatClicked
                break;
            case CHANNEL_NAME.VOICE:
                event = OnVoiceClicked
                break;
            case CHANNEL_NAME.SOCIAL:
                event = OnSocialClicked;
                break;
            case CHANNEL_NAME.EMAIL:
                event = OnEmailClicked;
                break;

            default:
                break;
        }
        if (!event) return;
        dispatch(channelButtonClicked(event))
    }

    const CHANNELS_STATUS_ALLOWING_CONTACTS = [
        CHANNEL_STATE.ACTIVE,
        CHANNEL_STATE.LOGGED_IN,
        CHANNEL_STATE.IDLE
    ]
    const channelBtn = (channel: AgentChannel, index: number) => {

        let icon = null;
        let label = "";
        let fill = agentCommandEnabledStateMap(channel.on) ? (CHANNELS_STATUS_ALLOWING_CONTACTS.indexOf(channel.state) > -1 ? "#FCFCFF" : "#888F9D") : "#888F9D";

        switch (channel.name) {
            case CHANNEL_NAME.CHAT:
                {
                    icon = <ChatIcon fill={fill} />
                    label = 'Chat'
                    break;
                }
            case CHANNEL_NAME.VOICE:
                {
                    icon = <VoiceIcon fill={fill} />
                    label = 'Voice'
                    break;
                }
            case CHANNEL_NAME.SOCIAL:
                {
                    icon = <SocialIcon fill={fill} />
                    label = 'Social'
                    break;
                }
            case CHANNEL_NAME.EMAIL:
                {
                    icon = <EmailIcon fill={fill} />
                    label = 'Email'
                    break;
                }
            default:
                {
                    icon = null
                    label = 'Unknown'
                    break;
                }
        }
        return (
            <button key={index}
                className={`${styles.channel} ${ (CHANNELS_STATUS_ALLOWING_CONTACTS.indexOf(channel.state) > -1) && styles.logged_in} my_btn`}
                onClick={() => handleChannelClick(channel)}
                disabled={!agentCommandEnabledStateMap(channel.on)}
            >
                {icon}
                <div className={styles.channel_label}>

                    <FormattedMessage
                        id={`agentChannels_${channel}`}
                        description={`Channel name for ${channel}`}
                        defaultMessage={label}
                    />
                </div>

            </button>
        )
    }

    return (
        <section className={styles.wrapper}>
            <div className={styles.channels_wrapper + `${channels.length === 3 ? " flex__nowrap" : ""}`}>
                {channels?.length > 0 && channels.map((channel, index) => (channelBtn(channel, index)))}
                {channels?.length === 0 &&
                    <div className={styles.channel}>
                        <span><FormattedMessage
                            id='agentChannels_no_channels'
                            description='No channels available for this agent'
                            defaultMessage='no channels'
                        /></span>
                    </div>
                }
            </div>
        </section>
    );
};

export default AgentChannels;
