import { addListener } from "@reduxjs/toolkit";
import { useEffect } from "react";
import { notifyOutboundMessage } from "../../../features/hub/OutboundSlice";
import { useAppDispatch } from "./hooks";


// dispatch async actions

const useOutboundMiddleware = (emit: (data:object) => void) => {

    const dispatch = useAppDispatch();

    useEffect(() => {

        const unsubscribe = dispatch(addListener(
            {
                actionCreator: notifyOutboundMessage,
                effect: (action, listenerApi) => {
                    console.log("emit: ", JSON.stringify(action.payload))
                    emit(action.payload);
                },
            }))
        // detach listener on unmount
        return unsubscribe
    }, [])


}

export default useOutboundMiddleware;