import './App.css';
import { useInOutImplementation } from './app/core/hooks/useInOutImplementation';
import useOutboundMiddleware from './app/core/hooks/useOutboundMiddleware';
import Phonebar from './features/phoneBar/Phonebar';
import LoginView from './features/login/Login';
import { IntlProvider } from 'react-intl';
import useMessages from './features/lang/useMessages';
import acceptLanguage from 'accept-language';
import PhoneInteraction from './features/phoneInteraction/PhoneInteraction';
import ChatInteraction from './features/chatInteraction/ChatInteraction';


const App: React.FC<{}> = () => {
  const { emit } = useInOutImplementation()
  useOutboundMiddleware(emit)

  acceptLanguage.languages(['en-US', 'en-GB', 'en', 'it-IT', 'it']);
  const useLocale = new Intl.Locale(acceptLanguage.get(navigator.language) || 'en-US');
  const useLanguage = useLocale.language || 'en';
  const messages = useMessages(useLanguage);

  // render components based on initialization state

  return (
    <IntlProvider locale={useLanguage} defaultLocale='en' messages={messages}>
      <LoginView />
      <Phonebar />
      {/* <PhoneInteraction /> */}
      {/* <ChatInteraction /> */}
    </IntlProvider>
  );

}

export default App;
