import { PropsWithChildren, useEffect, useRef } from "react";
import { useAppDispatch, useAppSelector } from "../../app/core/hooks/hooks";
import { useInboundMiddleware } from "../../app/core/hooks/useInboundMiddleware";

import useRegisteredEventMiddleware from "../../app/core/hooks/useRegisteredEventMiddleware";

import { FormattedMessage } from "react-intl";
import Draggable from "../../app/core/components/Draggable";
import AgentChannels from "../agentChannels/AgentChannels";
import AgentCommands from "../agentCommands/AgentCommands";
import AgentState from "../agentState/AgentState";
import Logo from "../logo/Logo";
import Separator from "../separator/Separator";
import styles from "./Phonebar.module.scss";
import { hidePhonebar, showPhonebar } from "./PhonebarSlice";
import WorkItemList from "../workItemList/WorkItemList";
import ServerNotification from "../notification/ServerNotification";


const Phonebar: React.FC<PropsWithChildren<{}>> = () => {

  enum PHONE_BAR_ACTION_CODE {
    phonebar_show = 'phonebar_show',
    phonebar_hide = 'phonebar_hide',

  }

  const [setActions] = useRegisteredEventMiddleware();
  const [setHubEvents] = useInboundMiddleware();
  const elmtRef = useRef<HTMLDivElement>(null);

  const show = useAppSelector((state) => state.phoneBar.showPhonebar);

  const dispatch = useAppDispatch();

  const phonebarHubEvents: { [key: string]: Function } = {
    [PHONE_BAR_ACTION_CODE.phonebar_hide]: (params: any) => {
      dispatch(hidePhonebar())
    },
    [PHONE_BAR_ACTION_CODE.phonebar_show]: (params: any) => {
      dispatch(showPhonebar())
    }
  }

  useEffect(() => {
    // register actions to be dispatched to hub
    setActions([])
    // subscribe to hub events
    setHubEvents(phonebarHubEvents)
  }, [])

  return (
    <>
      {show &&
        <Draggable elmnt={elmtRef}>

          <aside ref={elmtRef} className={styles.wrapper} role='application'>

            <ServerNotification />

            <div className={styles.phonebar_main_container}>
              <header className="header">
                {/*    <button title="minimize" className={styles.command + " my_btn"}>
                <MinimizeIcon fill="#FCFCFF" />
              </button>
              <button title="close" className={styles.command + " my_btn"}>
                <CloseIcon fill="#FCFCFF" />
              </button> */}
              </header>
              <div className={styles.content}>
                <Logo />
                <div className={styles.app_version}><FormattedMessage
                  id='version'
                  description='Application version label'
                  defaultMessage='version'
                /> 8.5.3</div>
                <Separator />
                <AgentState />
                <AgentChannels />
                <AgentCommands />
              </div>
            </div>

            <div className={styles.workItemList_container}>
              <WorkItemList />
            </div>

          </aside>

        </Draggable>
      }
    </>
  );
}

export default Phonebar
